import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Paper from '@material-ui/core/Paper';
import Fade from 'react-reveal/Fade';
import { Link, animateScroll as scroll } from "react-scroll";

const styles = {
    root: {
        flexGrow: 1,

    },
    grow: {
        marginRight: 20,

    },

    navButton: {
        marginLeft: 30,
        fontWeight: 300,
        transition:"1s",
        '&:hover': {
            color: '#74AA9D',
            paddingRight:20,
            paddingLeft: 20,
        }
    },

    active:{
        color:"red"
    },

    

    menuButton: {
        marginLeft: -12,
        marginRight: 20,
        color: "#75A99D",
    },

    transparentAppBar: {
        backgroundColor: "rgba(50,50,51,0.8)",

    },

    primaryButton: {
        fontSize: 18,
        fontWeight: 400,
    },
};

function handleSetActive(){
    console.log("active");
}

function handleSetInactive() {
    console.log("inActive");
}

function ButtonHeader(props) {
    const { classes } = props;
    return (

        <div className={classes.root}>

            <AppBar position="fixed" className={classes.transparentAppBar}>
                <Toolbar>
                    <IconButton className={classes.menuButton} aria-label="Menu">
                        <MenuIcon />
                    </IconButton>
                    <Link
                        activeClass="active"
                        to="section1"
                        spy={true}
                        smooth={true}
                        offset={0}
                        duration={500}
                        clickable
                    >
                        <Button color="inherit" className={classes.primaryButton}>
                            Gavin Craig
          </Button>
                    </Link>
                    <div className={classes.navLinks}>
                        <Link
                            activeClass="active"
                            to="section2"
                            spy={true}
                            smooth={true}
                            offset={0}
                            duration={500}
                        ><Button color="inherit" className={classes.navButton}>About</Button></Link>
                        <Link
                            activeClass="active"
                            to="section3"
                            spy={true}
                            smooth={true}
                            offset={50}
                            duration={500}
                        ><Button color="inherit" className={classes.navButton}>Techology</Button></Link>
                        <Link
                            activeClass="active"
                            to="section4"
                            spy={true}
                            smooth={true}
                            offset={100}
                            duration={500}
                        ><Button color="inherit" className={classes.navButton}>Projects</Button></Link>
                        <Link
                            activeClass="active"
                            to="section5"
                            spy={true}
                            smooth={true}
                            offset={0}
                            duration={500}
                        ><Button color="inherit" className={classes.navButton}>Contact</Button></Link>
                    </div>
                </Toolbar>
            </AppBar>

        </div>


    );
}

ButtonHeader.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ButtonHeader);