import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Button from "@material-ui/core/Button";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Grid from "@material-ui/core/Grid";
import IconButton from "@material-ui/core/IconButton";
import ProfileImage from "../ProfileImage/ProfileImage.js";
import { CardHeader } from "@material-ui/core";
import { DiGithubBadge } from "react-icons/di";
import { MdEmail } from "react-icons/md";
import Flip from "react-reveal/Flip";
import Fade from "react-reveal/Fade";
import { endianness } from "os";
import Image from "../honours-app-3-displays-01.png";
import app from "../swshr-jumpers.jpg"; // Tell Webpack this JS file uses this image

const styles = theme => ({
    root: {
        flexGrow: 1,
        justify: "center",
        alignItems: "center",
        padding: "5%",
        minHeight: "100vh"
    },

    nowrap: {
        whiteSpace: "nowrap",
        wordBreak: "keep-all"
    },

    introContainer: {
        padding: "5%",
        alignItems: "center",
        justifyContent: "center",
        marginTop: "10vh",
        marginLeft: "10%",
        marginRight: "10%",
        maxHeight:"90vh",
        backgroundColor: "rgb(33, 33, 33)",
        border:"none",
        overflowY: "auto",
        
    },



    paper: {
        padding: theme.spacing.unit * 2,
        textAlign: "center",
        color: theme.palette.text.secondary
    },
    paperBg: {
        width: "100%",
        height: "100%",
        backgroundImage: `url(${Image})`,
        backgroundSize: "cover",
        backgroundPosition: "absolute",
        top: 0,
        left: 0
    },

    splashContainer: {
        minHeight: "90vh",
        paddingTop: "20%",
        justify: "center",
        alignItems: "center"
    },

    cardParagraphs: {
        justify: "center",
        justifyContent: "center",
        alignItems: "start",
        textAlign: "start",
        paddingLeft: 40,
        width: "100%",
        paddingTop: 10
    },

    paragraphText: {
        fontSize: 18,
        fontWeight: 200,
        color: "#fefefe",
        marginBottom: 20,
    },

    title: {
        color: "#fefefe",
        fontWeight: 200
    },

    subHeader: {
        color: "rgba(209, 209, 209)",
        fontWeight: 100
    },

    contactDetail: {
        color: "rgba(209, 209, 209)",
        fontWeight: 100,
        textAlign: "start",

        padding: 10
    },

    button: {
        color: "rgba(209, 209, 209)",

        padding: 5,
        fontSize: 24,
        fontWeight: 300
    },

    gitButton: {
        color: "rgba(209, 209, 209)",
        fontSize: 45,

        padding: 0
    },

    container: {
        alignItems: "center",
        justifyContent: "center",

    },

    emailIcon: { margin: theme.spacing.unit },

    gitIcon: { fontSize: 43 },

    rightIcon: {
        marginLeft: theme.spacing.unit
    },

    media: {
        height: 100
    },

});

function HonoursModal(props) {
    const { classes } = props;
    return (

        <Paper className={classes.introContainer} style={{ alignItems: "center", justifyContent: "center" }}>
            <Grid container spacing={24}>
                <Grid item xs={12}>
                    <CardHeader
                        classes={{
                            title: classes.title,
                            subheader: classes.subHeader
                        }}
                        titleTypographyProps={{ variant: "h2", gutterBottom: "true" }}
                        title="SWSHR LTD."
                        subheader="Urban clothing brand"
                        subheaderTypographyProps={{
                            variant: "h4",
                            gutterBottom: "true"
                        }}
                    />
                </Grid>

                <Grid item lg={7} style={{ textAlign: "start", padding: 30 }}>
                    <Typography
                        component="p"
                        gutterBottom
                        className={classes.paragraphText}
                    >
                        An independent urban clothing brand started by a friend and I towards the end of 2017.
            </Typography>
                    <Typography
                        component="p"
                        className={classes.paragraphText}
                        gutterBottom
                    >
                        As co-founder, my role within the company was to establish business procedures and best practises to create the foundation for the brand.
                       SWSHR LTD. was registered as a private limited company in 2018.
            </Typography>

                    <Typography
                        component="p"
                        className={classes.paragraphText}
                        gutterBottom
                    >
                        The experience of starting a company allowed me to gain experience in many fields as I was responsible for: creating and managing the website, 
                     managing social media accounts, forming an early brand identity, sourcing manufacturers, shooting and editing much of the promotional material and collaborating to design products.
            </Typography>

                    <Typography
                        component="p"
                        className={classes.paragraphText}
                        gutterBottom
                    >
                                     I resigned as company director in 2019 in order to focus on finishing my degree and to allow myself time to work towards starting new ventures. The experience of 
planning and launching a small business was extremely fulfilling and insightful.  
            </Typography>

                    <Typography component="p" className={classes.paragraphText}>
                      SWSHR is now developed entirely by the co-founder - though I occasionally provide technical support when necessary.
            </Typography>
                </Grid>

                <Grid item lg={5} style={{ textAlign: "start" }}>
                    <img
                        src={app}
                        alt="Flowers in Chania"
                        width="auto"
                        height="450"
                    />
                </Grid>
            </Grid>
        </Paper>

    );
}

HonoursModal.propTypes = {
    classes: PropTypes.object.isRequired
};

export default withStyles(styles)(HonoursModal);
