import React from "react";
import PropTypes from "prop-types";
import {
  withStyles,
  MuiThemeProvider,
  withTheme
} from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import Image from "../honours-project-displays.jpg";
import Paper from "@material-ui/core/Paper"
import Modal from "@material-ui/core/Modal";
import HonoursModal from "./HonoursModal.js";

const styles = {
  card: {
    maxWidth: 805,
    backgroundColor: "rgb(35, 35, 36)"
  },
  media: {
    height: 303
  },
  title: {
    color: "#fefefe",
    fontWeight: 300
  },

  subHeader: {
    color: "rgba(209, 209, 209)",
    fontWeight: 400
  },

  paragraph: {
    color: "rgba(209, 209, 209)",
    fontWeight: 350
  }
};

class HonoursCard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false
    };
  }


  handleOpen = () => {
    this.setState({ open: true });
  };

  handleClose = () => {
    this.setState({ open: false });
  };

  render() {
    const { classes } = this.props;
    return (
      <div>
        <Modal
          
          aria-labelledby="simple-modal-title"
          aria-describedby="simple-modal-description"
          open={this.state.open}
          onClose={this.handleClose}
        >
          <HonoursModal/>
        </Modal>
        <Card className={classes.card}>
          <CardActionArea>
            <CardMedia
              className={classes.media}
              image={Image}
              titleTypographyProps={{ variant: "h2", gutterBottom: "true" }}
              title="Honours project app"
              alt="Image depicting screens from honours project diabetes applicaiton"
            />
            <CardContent>
              <Typography
                gutterBottom
                variant="h5"
                component="h2"
                className={classes.title}
                color="secondary"
              >
                Daily Blood Glucose Logger
              </Typography>

              <Typography
                gutterBottom
                variant="subheading"
                className={classes.subHeader}
                component="h2"
              >
                A mobile application for people living with type 1 diabetes.
                Developed for my honours project.
              </Typography>
              <Typography
                component="p"
                gutterBottom
                className={classes.paragraph}
              >
                React Native, NodeJS and MySQL.
              </Typography>
              
            </CardContent>
          </CardActionArea>
          <CardActions>
            <Button
              size="small"
              color="secondary"
              onClick={this.handleOpen.bind(this)}
            >
              Learn More
            </Button>
          </CardActions>
        </Card>
      </div>
    );
  }
}

HonoursCard.propTypes = {
  classes: PropTypes.object.isRequired
};

const SimpleModalWrapped = withStyles(styles)(HonoursCard);

export default withStyles(styles)(HonoursCard);
