import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Button from "@material-ui/core/Button";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Grid from "@material-ui/core/Grid";
import Image from "../architecture-astronomy-bird-s-eye-view-1820563.jpg";
import IconButton from "@material-ui/core/IconButton";
import ProfileImage from "../ProfileImage/ProfileImage.js";
import { CardHeader } from "@material-ui/core";
import { DiGithubBadge } from "react-icons/di";
import { MdEmail } from "react-icons/md";
import Flip from "react-reveal/Flip";
import Fade from "react-reveal/Fade";
import { endianness } from "os";

const styles = theme => ({
  root: {
    flexGrow: 1,
    justify: "center",
    alignItems: "center",
    
    backgroundImage: `url(${Image})`,
    backgroundSize: "cover",
    backgroundRepeat: "no-repeat",
    backgroundPosition: "bottom",
    Height: "60vh",
    minWidth: "100vw",
    margin: "0 auto"

  },

  introContainer: {
    backgroundColor: "rgb(33, 33, 33,0.9)",
    marginTop:"20%",
    marginBottom: "20%",
  },

  paragraphText: {
    fontWeight: 350
  },

  title: {
    color: "#fefefe",
    fontWeight: 200,
    fontSize: "3rem"
  },

  subHeader: {
    color: "rgba(209, 209, 209)",
    fontWeight: 100
  },

  contactDetail: {
    color: "rgba(209, 209, 209)",
    fontWeight: 100,
    textAlign: "start"
  },

  button: {
    color: "rgba(209, 209, 209)",
    padding: "1%",
    fontSize: 24,
    fontWeight: 300
  },

  gitButton: {
    fontSize: 45,
    padding: 0
  },

  container: {
    alignItems: "center",
    justifyContent: "center"
  },

  emailIcon: { margin: theme.spacing.unit },

  gitIcon: { fontSize: 43 }
});

function Contact(props) {
  const { classes } = props;
  return (
    <div className={classes.root} id="section5">
      <div style={{ maxWidth: 1920, margin: "0 auto" }}>
        <Flip top delay={250}>
          <Grid container justify="center" alignItems="center" spacing={0}>
            <Grid item xs={12} xl={6} alignItems="center" justify="center">
              <Card className={classes.introContainer}>
                <CardHeader
                  classes={{
                    title: classes.title,
                    subheader: classes.subHeader
                  }}
                  titleTypographyProps={{ variant: "h2", gutterBottom: "true" }}
                  title="Contact Details"
                  subheaderTypographyProps={{ variant: "subheading" }}
                  subheader="Gavin Craig | Computer Science Graduate | Aspiring Full Stack Developer"
                  avatar={<ProfileImage style={{ padding: 0, margin: 0 }} />}
                />

                <div>
                  <CardContent>
                    <Grid
                      container
                      spacing={8}
                      className={classes.cardParagraphs}
                    >
                      <Grid item lg={6} style={{ textAlign: "end" }}>
                        <a
                          href=" mailto:gcraig197@gmail.com"
                          style={{ textDecoration: "none" }}
                        >
                          <Button
                            variant="contained"
                            color="primary"
                            className={classes.button}
                          >
                            <MdEmail className={classes.emailIcon} />
                            Gcraig197@gmail.com
                          </Button>
                        </a>
                      </Grid>
                      <Grid item lg={6} style={{ textAlign: "center" }}>
                        <a
                          href="https://github.com/gavincraig"
                          target="_blank"
                          rel="noreferrer"
                          style={{ textDecoration: "none" }}
                        >
                          <Button
                            variant="contained"
                            color="primary"
                            className={classes.button}
                          >
                            <DiGithubBadge className={classes.gitIcon} />
                            /gavincraig
                          </Button>
                        </a>
                      </Grid>
                    </Grid>
                  </CardContent>
                </div>
              </Card>
            </Grid>
          </Grid>
        </Flip>
      </div>
    </div>
  );
}

Contact.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(Contact);
