import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Grid from "@material-ui/core/Grid";
import Image from "../apartment-chair-contemporary-509922.jpg";
import ProfileImage from "../ProfileImage/ProfileImage.js";
import { CardHeader } from "@material-ui/core";
import Fade from 'react-reveal/Fade';
import { display } from '@material-ui/system';


const styles = theme => ({
  root: {
    flexGrow: 1,
    justify: "center",
    alignItems: "center",
    backgroundColor: "blue",
    backgroundImage: `url(${Image})`,
    backgroundSize: "cover",
    backgroundRepeat: "no-repeat",
    backgroundPosition: "bottom",
    minHeight: "100vh",
    minWidth:"100vw",
    margin: "0 auto"
    
  },

  introContainer: {
    padding: "0",
    alignItems: "center",
    justifyContent: "center",
    marginTop: "10vh",
    maxWidth:1400,
    backgroundColor: "rgb(169, 158, 136,0.2)"
  },
  

  paper: {
    padding: theme.spacing.unit * 2,
    textAlign: "center",
    color: theme.palette.text.secondary
  },
  paperBg: {
    width: "100%",
    height: "100%",
    backgroundImage: `url(${Image})`,
    backgroundSize: "cover",
    backgroundPosition: "absolute",
    top: 0,
    left: 0
  },

  splashContainer: {
    minHeight: "90vh",
    paddingTop: "20%",
    justify: "center",
    alignItems: "center"
  },

  cardParagraphs: {
    justify: "center",
    justifyContent: "center",
    alignItems: "start",
    textAlign: "start",
    paddingLeft: "1%",
    width: "100%",
    paddingTop: 10
  },

  paragraphText: {
    fontWeight: 350
  }
});

function Intro(props) {
  const { classes } = props;
  return (
    <div className={classes.root} id="section2">
      
      <div style={{ maxWidth: 1920, margin: "0 auto"}}>
          <Fade up>
            <Grid container justify="center" alignItems="center">
            <Grid item xs={12} xl={6} alignItems="center" justify="center">
          <Card className={classes.introContainer}>
            <CardHeader
              titleTypographyProps={{ variant: "h2", gutterBottom: "true" }}
              title="About Me"
              subheaderTypographyProps={{ variant: "subheading" }}
              subheader="An open minded, creative individual who enjoys socialising, learning, and working with others."
            avatar={<ProfileImage display={{ xs: 'none', sm: 'block' }} />}
            />

            <div>
              <CardContent>
                <Grid
                alignItems="center"
                justify="center"
                  container
                  spacing={24}
                  className={classes.cardParagraphs}
                >
                  <Grid item md={6}>
                    <Typography
                      variant="h2"
                      gutterBottom
                      style={{ fontSize: 32, fontWeight: 350 }}
                    >
                      Interests
                    </Typography>

                    <Typography
                      component="p"
                      gutterBottom
                      className={classes.paragraphText}
                    >
                      I am a tech enthusiast, particularly interested in
                      "bleeding edge" developments.
                    </Typography>
                    <Typography
                      component="p"
                      gutterBottom
                      style={{ marginTop: 10 }}
                      className={classes.paragraphText}
                    >
                      I thoroughly enjoy learning and spend a lot of my time
                      expanding my knowledge on a broad range of topics.
                    </Typography>

                    <Typography
                      component="p"
                      style={{ marginTop: 10 }}
                      className={classes.paragraphText}
                    >
                      Besides tech, I am extremely interested in the areas of
                      fashion, travel, design and video editing.
                    </Typography>
                  </Grid>
                  <Grid item md={6}>
                    <Typography
                      variant="h2"
                      gutterBottom
                      style={{ fontSize: 32, fontWeight: 350 }}
                    >
                      Education
                    </Typography>

                    <Typography
                      component="p"
                      gutterBottom
                      className={classes.paragraphText}
                    >
                      2:1 BSc Honours Computer Science graduate from the
                      University of Strathclyde.
                    </Typography>

                    <Typography component="p" />
                  </Grid>
                </Grid>
              </CardContent>
            </div>
          </Card>
          </Grid>
          </Grid>
          </Fade>
        
      </div>
    </div>
  );
}

Intro.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(Intro);
