import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Image from '../cup-desk-drink-434337.jpg';

const styles = theme => ({
    root: {
        flexGrow: 1,
        minWidth:100,
        minHeight:600,
        justify:"center",
        alignItems:"center",
        
 
    },
    paper: {
        padding: theme.spacing.unit * 2,
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    paperBg:{
        width:"100%",
        height:"100%",
        backgroundImage: `url(${Image})`,
        backgroundSize: "cover",
        backgroundPosition:"absolute",
        top:0,
        left:0,
    },

    splashContainer:{
        minHeight: "90vh",
        paddingTop:"20%",
        justify:"center",
        alignItems:"center",
    
    },

    mainTypography:{
        color:"red",
    },

 
});

function SplashScreen (props) {
    const { classes } = props;
    return (
        <div className={classes.root}>
            <Paper className={classes.paperBg}>
            <Grid 
            className={classes.splashContainer}
            container 
         
            spacing={24}
            direction="column"
            justify="center"
            alignItems="center"
            >
                
                <Grid item xs={12}>
      
                        <Typography variant="h2" className={classes.mainTypography}>
                            Gavvvn Crerig
                        </Typography>
            
                </Grid>
                <Grid item xs>
             
                        <Typography variant="subtitle1">
                            BSC Computer Science Grad from the University of Strathclyde.
                        </Typography>
                    
                </Grid>
               
            </Grid>
            </Paper>
        </div>
    );
}

SplashScreen.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(SplashScreen);